#wrapper {
    overflow: hidden;
}

.body_lock {
    overflow: hidden;
}

.container {
    width: 100%;
    max-width: 1400px;
    margin: 0 auto;
    padding: 0 20px;

    @include media ("<tablet") {
        padding: 0 10px;
    }
}

/* ----------.main-section------------ */

.main-section {
    position: relative;
    min-height: 1785px;
    max-width: 2000px;
    margin: 0 auto;

    @include media ('<laptop') {
        min-height: 1205px;
    }

    @include media ('<widescreen') {
        min-height: 940px;
    }

    @include media ('<tablet') {
        min-height: 640px;
    }

    &::after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 450px;
        background: rgb(255, 255, 255);
        background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(254, 254, 254, 1) 100%);
        z-index: 4;

        @include media ('<tablet') {
            height: 200px;
        }

        @include media ('<tablet') {
            height: 150px;
            bottom: 5px;
        }
    }

    .text {
        position: absolute;
        text-align: center;
        z-index: 2;
        top: 415px;
        left: 50%;
        transform: translate(-50%);
        width: 100%;
        padding: 0 20px;

        @include media ('<laptop') {
            top: 220px;
        }

        @include media ('<widescreen') {
            top: 120px;
        }

        @include media ('<tablet') {
            top: 160px;
        }
    }

    .title {
        line-height: 1.2;
        color: #FEF5F5;
        margin-bottom: 10px;
        @include adaptiv-vaule("font-size", 128, 30, 1);

        @include media ('<phone') {
            font-size: 40px;
        }
    }

    .sub-title {
        color: #FEF5F5;
        margin: 0;
        @include adaptiv-vaule("font-size", 36, 20, 1);
    }

    #sky,
    #m1,
    #m2,
    #m3,
    #cloud1,
    #cloud2,
    #cloud3 {
        position: absolute;
    }

    #sky,
    #m1,
    #m2,
    #m3 {
        min-width: 2000px;
        left: 50%;
        transform: translate(-50%);

        @include media ('<laptop') {
            min-width: 1440px;
        }

        @include media ('<widescreen') {
            min-width: 1200px;
        }

        @include media ('<tablet') {
            min-width: 768px;
        }
    }

    #sky {
        top: 0;
    }

    #m1 {
        top: 325px;
        z-index: 1;

        @include media ('<laptop') {
            top: 235px;
        }

        @include media ('<widescreen') {
            top: 195px;
        }

        @include media ('<tablet') {
            top: 125px;
        }
    }

    #m2 {
        top: 475px;
        z-index: 3;

        @include media ('<laptop') {
            top: 350px;
        }

        @include media ('<widescreen') {
            top: 265px;
        }

        @include media ('<tablet') {
            top: 185px;
            z-index: 1;
        }
    }

    #m3 {
        top: 390px;
        z-index: 3;

        @include media ('<laptop') {
            top: 200px;
        }

        @include media ('<widescreen') {
            top: 100px;
        }
    }

    #cloud1 {
        min-width: 1100px;
        left: -60px;
        bottom: 340px;
        z-index: 4;

        @include media ('<laptop') {
            min-width: 600px;
            max-width: 600px;
            bottom: 240px;
        }

        @include media ('<widescreen') {
            min-width: 400px;
            max-width: 400px;
        }

        @include media ('<tablet') {
            min-width: 300px;
            max-width: 300px;
            bottom: 80px;
        }

        @include media ('<phone') {
            min-width: 300px;
            max-width: 300px;
        }
    }

    #cloud2 {
        min-width: 1300px;
        left: -100px;
        bottom: -325px;
        z-index: 4;

        @include media ('<laptop') {
            min-width: 900px;
            max-width: 900px;
            bottom: -240px;
        }

        @include media ('<widescreen') {
            min-width: 700px;
            max-width: 700px;
            bottom: -240px;
        }

        @include media ('<tablet') {
            min-width: 500px;
            max-width: 500px;
            bottom: -140px;
        }

        @include media ('<phone') {
            min-width: 300px;
            max-width: 300px;
            bottom: -70px;
        }
    }

    #cloud3 {
        min-width: 1300px;
        right: -300px;
        bottom: -15px;
        z-index: 4;

        @include media ('<laptop') {
            min-width: 1000px;
            max-width: 1000px;
        }

        @include media ('<widescreen') {
            min-width: 700px;
            max-width: 700px;
            bottom: -40px;
        }

        @include media ('<tablet') {
            min-width: 500px;
            max-width: 500px;
            right: -100px;
            bottom: -40px;
        }

        @include media ('<phone') {
            min-width: 300px;
            max-width: 300px;
            bottom: 0px;
        }
    }
}

.about_us {
    /* min-height: 200vh; */
    @include adaptiv-vaule("padding-top", 80, 50, 1);
    @include adaptiv-vaule("padding-bottom", 100, 80, 1);


    .content {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include media ("<tablet") {
            flex-direction: column-reverse;
        }
    }

    .img_block {
        width: 49%;
        position: relative;

        @include media ("<tablet") {
            width: 100%;
        }

        .skier {
            position: absolute;
            bottom: -20%;
            right: -20px;

            @include media ("<desktop") {
                left: 50%;
                transform: translateX(-50%);
            }

            img {
                width: 100%;
                height: 100%;
            }
        }
    }

    .text_block {
        max-width: 530px;
        margin-left: 35px;

        @include media ("<desktop") {
            max-width: 50%;
        }

        @include media ("<tablet") {
            margin: 0 0 30px 0;
            max-width: 100%;
            text-align: center;
        }
    }

    .title {
        margin-bottom: 25px;
    }

    .description {
        @include adaptiv-vaule("font-size", 20, 16, 1);
        line-height: 28 / 20 * 100%;
    }
}

.gallery {
    @include adaptiv-vaule("padding-top", 150, 30, 1);
    @include adaptiv-vaule("padding-bottom", 100, 50, 1);
    background: $white_1;
    position: relative;
    z-index: 1;

    .container {

        @include media ("<phone") {
            padding: 0;
        }
    }



    &::before {
        content: '';
        position: absolute;
        top: -66px;
        left: 0;
        @include adaptiv-vaule("height", 150, 50, 1);
        width: 100vw;
        background: $purple;
        transform: skewY(-4deg);

        @include media ("<tablet") {
            top: -58px;
        }

        @include media ("<640px") {
            top: -48px;
        }

        @include media ("<phone") {
            top: -35px;
        }
    }

    .gallery_bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 2000px;
        z-index: -1;
        height: 2200px;
        background: $purple;

        &::after {
            content: '';
            position: absolute;
            left: 0;
            bottom: -5%;
            width: 100%;
            height: 400px;
            z-index: 0;
            background: $white_1;
            transform: skewY(4deg);
        }

        img {
            width: 100%;
            height: 100%;
        }
    }

    .content {
        text-align: center;
    }

    .title_gallery {
        margin-bottom: 50px;
        position: relative;
        @include adaptiv-vaule("padding-bottom", 70, 45, 1);

        &::after {
            content: '';
            position: absolute;
            width: 100%;
            height: 1px;
            background: $black;
            bottom: 0;
            left: 0;
        }

    }


    /*--------Переключатель-------*/

    .toggle {
        position: relative;

        .toggle-box {
            display: flex;
            align-items: center;
            justify-content: space-between;
            position: absolute;
            width: 300px;
            padding: 15px 50px;
            background: $purple;
            @include adaptiv-vaule("top", 80, 40, 1);
            left: 50%;
            transform: translateX(-50%);

            span {
                font-size: 20px;
            }
        }

        .switch {
            margin: 0 40px;
            display: none;

            &:checked+.switch_for {
                &::before {
                    left: 28px;
                }

                &::after {
                    background: $orange;
                }
            }
        }

        .switch_for {
            position: relative;
            cursor: pointer;
            width: 50px;
            height: 24px;

            &::after {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 50px;
                height: 24px;
                background: $blue;
                border-radius: 15px;
                transition: all 0.3s ease;
            }

            &::before {
                content: '';
                width: 20px;
                height: 20px;
                background: $white;
                border-radius: 50%;
                position: absolute;
                top: 2px;
                left: 2px;
                z-index: 1;
                transition: all 0.3s ease;
            }
        }
    }


    .gallery_block {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        row-gap: 40px;
        column-gap: 25px;
        @include adaptiv-vaule("margin-bottom", 260, 100, 1);

        @include media ("<960px") {
            grid-template-columns: 1fr 1fr;
            row-gap: 30px;
        }

        @include media ("<tablet") {
            row-gap: 25px;
            column-gap: 10px
        }

        @include media ("<phone") {
            grid-template-columns: 1fr;
            column-gap: 0;
        }
    }

    .set {
        height: 400px;
        width: 100%;
        border-radius: 15px;
        overflow: hidden;
        position: relative;

        @include media ("<tablet") {
            height: 300px;
        }

        video {
            width: 100%;
        }

        &:first-child {
            width: 100%;
            grid-column: span 2;
        }

        &:nth-child(n+7) {
            width: 100%;
            grid-column: span 2;
        }
    }


    .price_block {
        position: relative;
        z-index: 1;
        background: $blue_2;
        padding: 40px 30px;
        @include adaptiv-vaule("padding-top", 40, 10, 1);
        @include adaptiv-vaule("padding-bottom", 40, 10, 1);
        @include adaptiv-vaule("padding-right", 30, 10, 1);
        @include adaptiv-vaule("padding-left", 30, 10, 1);
        border-radius: 15px;
        overflow: hidden;

        .title_price {
            color: $white_1;
            @include adaptiv-vaule("margin-bottom", 50, 30, 1);
        }

        .offers {
            @include adaptiv-vaule("margin-bottom", 65, 35, 1);
        }

        .offer_title {
            background: $orange;
            display: inline-block;
            border-radius: 15px;
            padding: 10px 30px;
        }

        .offers_box {
            border: 3px solid $orange;
            border-radius: 15px;
            @include adaptiv-vaule("padding-top", 30, 10, 1);
            @include adaptiv-vaule("padding-bottom", 30, 10, 1);
            @include adaptiv-vaule("padding-right", 30, 10, 1);
            @include adaptiv-vaule("padding-left", 30, 10, 1);
            display: flex;
            align-items: center;
            justify-content: space-around;

            @include media ("<desktop") {
                flex-wrap: wrap;
            }
        }

        .box {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            margin: 0 10px;
            width: 315px;

            @include media ("<desktop") {
                width: 46%;

                &:not(:last-child) {
                    margin: 0 0 30px 0;
                }
            }

            @include media ("<phone_max") {
                width: 100%;
            }
        }

        .box_title {
            background: $white_1;
            padding: 10px 12px;
            width: 100%;
            font-weight: 600;
            border-radius: 15px;
        }

        .card {
            position: relative;
            border-radius: 15px;
            width: 100%;
            height: 400px;
            overflow: hidden;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            align-items: center;

            .ibg {
                z-index: -1;
            }
        }

        .card_content {
            background: $white_1;
            padding: 25px 30px;
            @include adaptiv-vaule("padding-top", 25, 10, 1);
            @include adaptiv-vaule("padding-bottom", 25, 10, 1);
            @include adaptiv-vaule("padding-right", 30, 15, 1);
            @include adaptiv-vaule("padding-left", 30, 15, 1);
            border-radius: 15px;
            width: 90%;
            @include adaptiv-vaule("margin-bottom", 50, 25, 1);

        }

        .info {
            display: flex;
            align-items: center;
            text-align: start;

            @include media ("<phone_max") {
                justify-content: center;
            }

            @include media ("<phone") {
                justify-content: flex-start;
            }

            &:not(:last-child) {
                margin-bottom: 10px;
            }

            img {
                margin-right: 10px;
            }

            span {
                font-size: 16px;
            }
        }

        .price_btn {
            background: $orange;
            font-size: 20px;
            border-radius: 40px;
            padding: 10px 20px;
            width: 200px;
            margin-bottom: 20px;
            font-weight: 600;
        }
    }

    .price_body {
        @include adaptiv-vaule("margin-bottom", 35, 15, 1);
    }

    .price_list_title {
        @include adaptiv-vaule("margin-bottom", 25, 15, 1);
        @include adaptiv-vaule("font-size", 24, 18, 1);
    }

    .price_item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        background: $purple;
        @include adaptiv-vaule("padding-top", 20, 10, 1);
        @include adaptiv-vaule("padding-bottom", 20, 10, 1);
        @include adaptiv-vaule("padding-right", 30, 10, 1);
        @include adaptiv-vaule("padding-left", 30, 10, 1);
        border-radius: 15px;

        &:not(:last-child) {
            margin-bottom: 10px;
        }
    }

    .position {
        @include adaptiv-vaule("font-size", 20, 15, 1);

        &:first-child {
            margin-right: 10px;
            white-space: nowrap;
        }
    }

    .visible {
        @include media ("<tablet") {
            display: none;
        }
    }

    .hidden_text,
    .hidden {
        display: none;

        @include media ("<tablet") {
            display: block;
            margin-bottom: 10px;
            font-size: 14px;
        }
    }

    .hidden {
        margin: 0;
    }

    .color {
        color: $orange;
    }

    .notice {
        background: $orange;
        padding: 0 5px;
        @include adaptiv-vaule("padding-top", 15, 5, 1);
        @include adaptiv-vaule("padding-bottom", 15, 5, 1);
        @include adaptiv-vaule("margin-right", -30, -10, 1);
        @include adaptiv-vaule("margin-left", -30, -10, 1);

        p {
            font-family: 'Cormorant';
            @include adaptiv-vaule("font-size", 20, 12, 1);
            font-weight: 600;
        }
    }
}

.reviews {
    padding: 25px 0;
    text-align: center;
    background: $purple;

    .reviews_title {
        @include adaptiv-vaule("margin-bottom", 50, 25, 1);
    }

    .swiper {

        .swiper-pagination {
            bottom: 0;
        }

        .swiper-pagination-bullet {
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background: #7D8993;
            opacity: 0.7;
        }

        .swiper-pagination-bullet-active {
            background: #3F454A;
            opacity: 1;
        }

        .swiper-wrapper {
            display: flex;
            align-items: flex-start;
            justify-content: flex-start;
            height: 250px;
            width: 1180px;
            margin: 0 auto;
        }

        .swiper-slide {
            display: flex;
            align-items: center;
            max-width: 510px;
            height: 140px;
            background: $white_1;
            border-radius: 15px;
            padding: 25px;
            margin: 0 25px;
        }

        .slide_img {
            width: 100px;
            height: 135px;
            border-radius: 15px;
            position: relative;
            overflow: hidden;
            margin-right: 25px;
        }

        .slider_text {
            width: 75%;
            text-align: start;
        }

        .name {
            margin-bottom: 25px;
            font-size: 20px;
            font-weight: 600;
        }

        .description {
            font-size: 18px;
        }

        .swiper-button-prev,
        .swiper-button-next {
            position: absolute;
            top: 40%;

        }

        .swiper-button-prev {
            left: 0;

            &:after {
                content: url(../images/reviews/arrow-prev.svg);
            }
        }

        .swiper-button-next {
            right: 0;

            &:after {
                content: url(../images/reviews/arrow-next.svg);
            }
        }
    }
}

.contact {
    padding: 25px 0 0 0;
    text-align: center;
    background: $gray_1;
    position: relative;
    z-index: 1;
    overflow: hidden;

    &::before {
        content: '';
        position: absolute;
        top: -140px;
        left: 0;
        background: $purple;
        width: 100%;
        height: 280px;
        transform: skewY(8deg);
        z-index: -1;
    }

    .container {

        @include media ("<phone") {
            padding: 0;
        }
    }

    .contact_title {
        margin-bottom: 25px;
    }

    .contact_body {
        background: #545B60;
        border-radius: 15px;
        padding: 40px 60px;
        @include adaptiv-vaule("padding-top", 40, 15, 1);
        @include adaptiv-vaule("padding-bottom", 40, 15, 1);
        @include adaptiv-vaule("padding-right", 60, 15, 1);
        @include adaptiv-vaule("padding-left", 60, 15, 1);
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include media ("<desktop") {
            flex-direction: column;
        }
    }

    .contact_content {
        width: 39%;

        @include media ("<desktop") {
            width: 80%;
            margin-bottom: 45px;
        }

        @include media ("<tablet") {
            width: 100%;
        }
    }

    .info {
        text-align: start;
        background: $white_1;
        padding: 15px 20px;
        @include adaptiv-vaule("padding-top", 15, 7, 1);
        @include adaptiv-vaule("padding-bottom", 15, 7, 1);
        @include adaptiv-vaule("padding-right", 20, 10, 1);
        @include adaptiv-vaule("padding-left", 20, 10, 1);
        width: 100%;
        margin-bottom: 15px;
        margin-right: 20px;
        border-radius: 15px;
        @include adaptiv-vaule("font-size", 18, 16, 1);


        .data {
            @include adaptiv-vaule("font-size", 18, 16, 1);
            margin-left: 10px;
        }
    }

    .hover {
        transition: all 0.3s ease;

        &:hover {
            background: $orange;
            transform: scale(1.035);
        }
    }

    .notice {
        color: $white;
        @include adaptiv-vaule("font-size", 20, 16, 1);
    }

    .social_links {

        .link_title {
            margin: 30px 0 15px 0;
        }

        .links {
            display: flex;
            align-items: center;
            justify-content: space-around;
            background: $white_1;
            @include adaptiv-vaule("padding-top", 10, 7, 1);
            @include adaptiv-vaule("padding-bottom", 10, 7, 1);
            @include adaptiv-vaule("padding-right", 10, 7, 1);
            @include adaptiv-vaule("padding-left", 10, 7, 1);
            border-radius: 15px;
            width: 100%;

            a {
                width: 35px;
                height: 35px;
                display: flex;
                align-items: center;
                justify-content: center;

                @include media ("<tablet") {
                    idth: 20px;
                    height: 20px;
                }

                &:not(:last-child) {
                    @include adaptiv-vaule("margin-right", 25, 20, 1);
                }
            }

            svg {
                width: 100%;
                height: 100%;
                fill: $gray_2;
                transition: all 0.3s ease;

                &:hover {
                    fill: $orange;
                    transform: scale(1.2);
                }
            }
        }

    }

    .contact_video {
        width: 50%;
        height: 260px;
        position: relative;

        @include media ("<desktop") {
            width: 100%;
            height: 300px;
        }

        @include media ("<tablet") {
            height: 250px;
        }

        video {
            border-radius: 15px;
            width: 100%;
            height: 100%;
        }

        .stiker {
            position: absolute;
            top: -27px;
            @include adaptiv-vaule("right", -30, 0, 1);
            background: $orange;
            border-radius: 15px;
            @include adaptiv-vaule("padding-top", 15, 7, 1);
            @include adaptiv-vaule("padding-bottom", 15, 7, 1);
            @include adaptiv-vaule("padding-right", 15, 10, 1);
            @include adaptiv-vaule("padding-left", 15, 10, 1);
            @include adaptiv-vaule("font-size", 20, 14, 1);

            @include media ("<phone") {
                right: 0;
            }
        }
    }
}

.map {
    width: 100%;
    height: 50vh;
    position: relative;

    iframe {
        width: 100%;
        height: 100%;
    }
}